<template>
  <div>
     <button
      type="button"
      @click="$router.go(-1)"
      class="btn btn-sm btn-style1 font-bold text-uppercase underline"
      style="position:absolute;z-index:100;"
    >
      <span class="text-xl typcn typcn-arrow-left"></span> Kembali
    </button>
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320" class="animated fadeInDown">
      <path
        fill="#0099ff"
        fill-opacity="1"
        d="M0,256L60,256C120,256,240,256,360,229.3C480,203,600,149,720,106.7C840,64,960,32,1080,69.3C1200,107,1320,213,1380,266.7L1440,320L1440,0L1380,0C1320,0,1200,0,1080,0C960,0,840,0,720,0C600,0,480,0,360,0C240,0,120,0,60,0L0,0Z"
      />
    </svg>
    <div class="row">
        <div class="offset-1 col-10">
            <div class="p-3 rounded-lg shadow-lg" style="border:1px solid grey;">
              <!-- <button type="button" @click="$router.push(`/laporan/lemburtotal?uid=${$store.state.users.uid}&tahun=${vdata.tahun}&bulan=${vdata.bulan}&total=${total}`)" class="btn btn-sm btn-success float-right  "><span class="typcn typcn-chart-bar text-green-400"></span> Laporan</button> -->
              <br>
                <div class="sm-form ">
                    <label for="tahun">Tahun</label>
                    <input type="text" id="tahun" name="tahun" class="form-control form-control-sm" placeholder="tahun" v-model="vdata.tahun" >
                </div>
                  <div class="sm-form ">
                    <label for="bulan">Bulan</label>
                    <select class='form-control' v-model="vdata.bulan">
                      <option>Januari</option>
                      <option>Februari</option>
                      <option>Maret</option>
                      <option>April</option>
                      <option>Mei</option>
                      <option>Juni</option>
                      <option>Juli</option>
                      <option>Agustus</option>
                      <option>September</option>
                      <option>Oktober</option>
                      <option>November</option>
                      <option>Desember</option>
                    </select>
                </div>
                <br>
                <hr class="style13 mt-2 mb-2">
              <div class="text-center font-bold text-uppercase font-times text-xl">
                Jumlah Ijin Bulan ini : <br>
                  <button type="button" class="btn btn-sm btn-style9 text-2xl ">
                    {{total}}
                  </button>
              </div>
                
            </div>
        </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
  var id = require("date-fns/locale/id");
  import autophp from '@/plugins/autophp.js'
  let sdb = new autophp()
export default{
  data(){
    return{
        vdata:{
            tahun:'2020',
            bulan:'M',
        },
        jumlah_lembur:0,
        datanya:[]
    }
  },
  computed:{
      total(){
          let data = this.td
          console.log('data',data)
          let jumlah= data.length
          return jumlah
      },
      td(){
          let that = this
          let data = this.datanya
          data = data.filter(e=>{
            console.log(that.tahuns(e.tanggal_ijin))
            console.log(that.bulans(e.tanggal_ijin))
            console.log(that.vdata.tahun)
            console.log(that.vdata.bulan)
              if(that.tahuns(e.tanggal_ijin).indexOf(that.vdata.tahun)!=-1 && that.bulans(e.tanggal_ijin).indexOf(that.vdata.bulan)!=-1){
                  return e
              }
          })
          return data
      }
  },
  methods:{ 
    tahuns(data) {
      return this.$datefns.format(
        new Date(data),
        "YYYY",
        {
          locale: id
        }
      );
    },
    bulans(data) {
      return this.$datefns.format(
        new Date(data),
        "MMMM",
        {
          locale: id
        }
      );
    },
    tahun() {
      return this.$datefns.format(
        new Date(),
        "YYYY",
        {
          locale: id
        }
      );
    },
    bulan() {
      return this.$datefns.format(
        new Date(),
        "MMMM",
        {
          locale: id
        }
      );
    },
  },
  mounted() {
      this.vdata.bulan=this.bulan()
      this.vdata.tahun=this.tahun()
      sdb.collection("ijin_keluar").doc().select(`select * from ijin_keluar where froms='${this.$route.query.id}'`).then(res => {
        this.datanya = res
        console.log(res)
      });
      
      // db.collection(`users/${this.$route.query.id}/history-lembur`).get().then(res=>{
      //     let data = res.docs.map(e=>e.data())
      // })
  },
}
</script>